@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
/* Poppins font */

/* reset */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Poppins', sans-serif;
}

.container {
  max-width: 500px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin: 25vh auto;
  overflow: auto;
  min-height: 300px;
  border: 2px solid steelblue;
  box-shadow: 5px 5px rgb(163, 163, 163);
  padding: 30px;
  border-radius: 50px;
}

.header {
  margin-bottom: 20px;
}

.footer {
  font-size: 0.5em;
}

button {
  background-color: white; /* Green */
  border: none;
  color: grey;
  padding: 3px 3px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 20px;
}

.timer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
}

.button-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-left: 10px;
}

.control-button {
  padding-top: 20px;
}

.cycle {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
}

